export default {
  methods: {
    colorBlend: function (percent, color1, color2, linear) {
      let c1, c2
      switch (arguments.length) {
        case 0:
          return null
          break
        case 1:
          return null
          break
        case 2:
          c1 = (arguments[1].charAt(0) === '#')? this.hexToRGBA(arguments[1]) : arguments[1]
          return this._Log_Shade(arguments[0], c1)
        case 3:
          c1 = (arguments[1].charAt(0) === '#')? this.hexToRGBA(arguments[1]) : arguments[1]
          c2 = (arguments[2].charAt(0) === '#')? this.hexToRGBA(arguments[2]) : arguments[2]
          return this._Log_Blend(arguments[0], c1, c2)
        case 4:
          if (arguments[3]) {
            if (arguments[2] === false) {
              c1 = (arguments[1].charAt(0) === '#')? this.hexToRGBA(arguments[1]) : arguments[1]
              return this._Linear_Shade(arguments[0], c1)
            } else {
              c1 = (arguments[1].charAt(0) === '#')? this.hexToRGBA(arguments[1]) : arguments[1]
              c2 = (arguments[2].charAt(0) === '#')? this.hexToRGBA(arguments[2]) : arguments[2]
              return this._Linear_Blend(arguments[0], c1, c2)
            }
          } else {
            if (arguments[2] === false) {
              c1 = (arguments[1].charAt(0) === '#')? this.hexToRGBA(arguments[1]) : arguments[1]
              return this._Log_Shade(arguments[0], c1)
            } else {
              c1 = (arguments[1].charAt(0) === '#')? this.hexToRGBA(arguments[1]) : arguments[1]
              c2 = (arguments[2].charAt(0) === '#')? this.hexToRGBA(arguments[2]) : arguments[2]
              return this._Log_Blend(arguments[0], c1, c2)
            }
          }
      }
    },
    _Linear_Blend: function (p,c0,c1) {
      var i=parseInt,r=Math.round,P=1-p,[a,b,c,d]=c0.split(","),[e,f,g,h]=c1.split(","),x=d||h,d=x?","+(!d?h:!h?d:r((parseFloat(d)*P+parseFloat(h)*p)*1000)/1000+")"):")";
      return"rgb"+(x?"a(":"(")+r(i(a[3]=="a"?a.slice(5):a.slice(4))*P+i(e[3]=="a"?e.slice(5):e.slice(4))*p)+","+r(i(b)*P+i(f)*p)+","+r(i(c)*P+i(g)*p)+d;
    },
    _Log_Blend: function (p,c0,c1) {
      var i=parseInt,r=Math.round,P=1-p,[a,b,c,d]=c0.split(","),[e,f,g,h]=c1.split(","),x=d||h,d=x?","+(!d?h:!h?d:r((parseFloat(d)*P+parseFloat(h)*p)*1000)/1000+")"):")";
      return"rgb"+(x?"a(":"(")+r((P*i(a[3]=="a"?a.slice(5):a.slice(4))**2+p*i(e[3]=="a"?e.slice(5):e.slice(4))**2)**0.5)+","+r((P*i(b)**2+p*i(f)**2)**0.5)+","+r((P*i(c)**2+p*i(g)**2)**0.5)+d;
    },
    _Linear_Shade: function (p,c) {
      var i=parseInt,r=Math.round,[a,b,c,d]=c.split(","),P=p<0,t=P?0:255*p,P=P?1+p:1-p;
      return"rgb"+(d?"a(":"(")+r(i(a[3]=="a"?a.slice(5):a.slice(4))*P+t)+","+r(i(b)*P+t)+","+r(i(c)*P+t)+(d?","+d:")");
    },
    _Log_Shade: function (p,c) {
      var i=parseInt,r=Math.round,[a,b,c,d]=c.split(","),P=p<0,t=P?0:p*255**2,P=P?1+p:1-p;
      return"rgb"+(d?"a(":"(")+r((P*i(a[3]=="a"?a.slice(5):a.slice(4))**2+t)**0.5)+","+r((P*i(b)**2+t)**0.5)+","+r((P*i(c)**2+t)**0.5)+(d?","+d:")");
    },
    hexToRGBA: function (c) {
      let color = c.slice(1)
      if (color.length === 6) {
        let r = parseInt(color.slice(0, 2), 16)
        let g = parseInt(color.slice(2, 4), 16)
        let b = parseInt(color.slice(4, 6), 16)
        return 'rbg(' + r + ',' + g + ',' + b + ')'
      } else if (color.length === 8) {
        let r = parseInt(color.slice(0, 2), 16)
        let g = parseInt(color.slice(2, 4), 16)
        let b = parseInt(color.slice(4, 6), 16)
        let a = parseInt(color.slice(6, 8), 16)
        return 'rbg(' + r + ',' + g + ',' + b + ',' + a + ')'
      } else {
        return null
      }
    }
  }
}