import Check from '@/assets/svg/icons/check.svg'
import Refresh from '../refresh-store'
export default {
  data () {
    return {
      check: Check,
      complete: -1
    }
  },
  props: {
    lock: {
      default: -1
    }
  },
  mounted: function () {
    let completion = (this.$el.getAttribute('complete') === null) ? -1 : this.$el.getAttribute('complete')
    if (completion !== -1) {
      completion = (completion === '')? '' : parseInt(this.$el.getAttribute('complete'))
    }
    this.complete = completion
    if (this.complete === '') {
      let keys = Object.keys(this.$store._modules.root._children)
      if (keys.includes('refresh') === false) {
        this.$store.registerModule('refresh', Refresh)
      }
      this.$store.dispatch('refresh')
    }
  },
  methods: {
    isLocked: function () {
      if (this.complete > 0 && this.lock !== -1) {
        let previousCompletion = this.$store.getters.getCompletionItem(this.complete - 1)
        if (previousCompletion < 1) {
          return true
        }
      }
      return false
    },
    setComplete: function (n) {
      if (this.complete !== -1) {
        let obj = {}
        obj.index = parseInt(this.complete)
        obj.value = Math.min(Math.max(n, 0), 1)
        this.$store.dispatch('setCompletionItem', obj)
      }
    },
    getComplete: function () {
      if (this.complete !== -1) {
        let completion = this.$store.getters.getCompletionItem(this.complete)
        return completion
      } else {
        return -1
      }
    }
  },
  computed: {
    getCompletion: function () {
      return this.getComplete()
    }
  }
}