export default {
  data () {
    return {
      viewport: false,
      viewportPosition: {x:0, y:0},
      scrollContainer: null
    }
  },
  mounted () {
    this.scrollContainer = this.getClosestElement(this.$el, '.scroll-container')
    this.viewport = this.checkViewportOverlap()
    this.scrollContainer.addEventListener('scroll', this.onScroll)
    window.addEventListener('resize', this.onResize)
    let viewportStatus = this.checkViewportOverlap()
    this.$emit('viewport-change', viewportStatus)
  },
  beforeDestroy () {
    this.scrollContainer.removeEventListener('scroll', this.onScroll)
    window.removeEventListener('resize', this.onResize)
  },
  methods: {
    getClosestElement (elem, selector) {
      // Element.matches() polyfill
      if (!Element.prototype.matches) {
        Element.prototype.matches =
          Element.prototype.matchesSelector ||
          Element.prototype.mozMatchesSelector ||
          Element.prototype.msMatchesSelector ||
          Element.prototype.oMatchesSelector ||
          Element.prototype.webkitMatchesSelector ||
          function(s) {
            let matches = (this.document || this.ownerDocument).querySelectorAll(s),
              i = matches.length;
            while (--i >= 0 && matches.item(i) !== this) {}
            return i > -1
          }
      }
      // Get closest match
      for ( ; elem && elem !== document; elem = elem.parentNode ) {
        if ( elem.matches( selector ) ) return elem
      }
      return null
    },
    onScroll () {
      let viewportStatus = this.checkViewportOverlap()
      if (this.viewport !== viewportStatus) {
        this.viewport = viewportStatus
        this.$emit('viewport-change', viewportStatus)
      } else {
        this.viewport = viewportStatus
      }
    },
    onResize () {
      let viewportStatus = this.checkViewportOverlap()
      if (this.viewport !== viewportStatus) {
        this.inViewport = viewportStatus
        this.$emit('viewport-change', viewportStatus)
      } else {
        this.viewport = viewportStatus
      }
    },
    checkViewportOverlap () {
      let bounding = this.$el.getBoundingClientRect()
      let containerBounding = this.scrollContainer.getBoundingClientRect()
      this.viewportPosition = {
        x: (bounding.left - containerBounding.right)/(containerBounding.width + bounding.width),
        y: -(bounding.top - containerBounding.bottom)/(containerBounding.height + bounding.height)
      }
      let inView = (
        ((bounding.top >= containerBounding.top && bounding.top <= containerBounding.bottom) ||
          (bounding.bottom >= containerBounding.top && bounding.bottom <= containerBounding.bottom)) &&
        ((bounding.left >= containerBounding.left && bounding.left <= containerBounding.right) ||
          (bounding.right >= containerBounding.left && bounding.right <= containerBounding.right))
      )
      if (inView) {
        this.$emit('viewport-position', this.viewportPosition)
      }
      return inView
    }
  },
  computed: {
    inViewport () {
      return this.viewport
    },
    getPosition () {
      return this.viewportPosition
    }
  }
}