export default {
  data () {
    return {
      activeItem: false
    }
  },
  props: {
    group: {
      default: ''
    }
  },
  created () {
    this.$root.$on('group-toggle', this.groupToggle)
  },
  beforeDestroy () {
    this.$root.$off('group-toggle', this.groupToggle)
  },
  methods: {
    groupToggle (g) {
      if (g === this.group && !this.activeItem) {
        this.active = false
      } else {
        this.activeItem = false
      }
    },
    broadcastToGroup () {
      if (this.group.length > 0) {
        this.activeItem = true
        this.$root.$emit('group-toggle', this.group)
      }
    }
  }
}