export default {
// t: current time, b: begInnIng value, c: change In value, d: duration
  linear (t, b, c, d) {
    return (c - b) * t / d + b;
  },
  delayed (t, b, c, d) {
    if((t / d) <= 0.2) {
      return b
    } else{
      return (c - b) * (t - 0.2) / (d * 0.8) + b;
    }
  },
  begin (t, b, c, d) {
    if((t / d) >= 0.5) {
      return c
    } else{
      return (c - b) * (t) / (d * 0.5) + b;
    }
  },
  end (t, b, c, d) {
    if((t / d) <= 0.5) {
      return b
    } else{
      return (c - b) * (t - 0.5) / (d * 0.5) + b;
    }
  },
  centerBack (t, b, c, d) {
    if((t / d) <= 0.3) {
      return (c - b) * t / (d * 0.3) + b;
    } else if ((t / d) >= 0.7){
      return (c - b) * (1 - ((t - 0.7) / (d * 0.3))) + b;
    } else {
      return c
    }
  },
  centerForward (t, b, c, d) {
    if((t / d) <= 0.3) {
      return ((c / 2) - b) * t / (d * 0.3) + b;
    } else if ((t / d) >= 0.7){
      return ((c / 2) - b) * (t - 0.7) / (d * 0.3) + b + (c / 2);
    } else {
      return c / 2
    }
  },
// Sine
  sineIn (t, b, c, d) {
    return -c * Math.cos(t / d * (Math.PI / 2)) + (c - b) + b;
  },
  sineOut (t, b, c, d) {
    return (c - b) * Math.sin(t / d * (Math.PI / 2)) + b;
  },
  sineInOut (t, b, c, d) {
    return -(c - b) / 2 * (Math.cos(Math.PI * t / d) - 1) + b;
  },
// Quintic
  quintIn (t, b, c, d) {
    return (c - b) * (t /= d) * t * t * t * t + b;
  },
  quintOut (t, b, c, d) {
    return (c - b) * ((t = t / d - 1) * t * t * t * t + 1) + b;
  },
  quintInOut (t, b, c, d) {
    if ((t /= d / 2) < 1) return (c - b) / 2 * t * t * t * t * t + b;
    return (c - b) / 2 * ((t -= 2) * t * t * t * t + 2) + b;
  },
// Quartic
  quartIn (t, b, c, d) {
    return (c - b) * (t /= d) * t * t * t + b;
  },
  quartOut (t, b, c, d) {
    return -(c - b) * ((t = t / d - 1) * t * t * t - 1) + b;
  },
  quartInOut (t, b, c, d) {
    if ((t /= d / 2) < 1) return (c - b) / 2 * t * t * t * t + b;
    return -(c - b) / 2 * ((t -= 2) * t * t * t - 2) + b;
  },
// Quadratic
  quadIn (t, b, c, d) {
    return (c - b) * (t /= d) * t + b;
  },
  quadOut (t, b, c, d) {
    return -(c - b) * (t /= d) * (t - 2) + b;
  },
  quadInOut (t, b, c, d) {
    if ((t /= d / 2) < 1) return (c - b) / 2 * t * t + b;
    return -(c - b) / 2 * ((--t) * (t - 2) - 1) + b;
  },
// Exponential
  expoIn (t, b, c, d) {
    return (t == 0) ? b : (c - b) * Math.pow(2, 10 * (t / d - 1)) + b;
  },
  expoOut (t, b, c, d) {
    return (t == d) ? b + (c - b) : (c - b) * (-Math.pow(2, -10 * t / d) + 1) + b;
  },
  expoInOut (t, b, c, d) {
    if (t == 0) return b;
    if (t == d) return c;
    if ((t /= d / 2) < 1) return (c - b) / 2 * Math.pow(2, 10 * (t - 1)) + b;
    return (c - b) / 2 * (-Math.pow(2, -10 * --t) + 2) + b;
  },
// Elastic
  elasticIn (t, b, c, d, a, p) {
    var s;
    if (t == 0) return b;
    if ((t /= d) == 1) return b + c;
    if (!p) p = d * .3;
    if (!a || a < Math.abs(c)) {
      a = c;
      s = p / 4;
    }
    else s = p / (Math.PI * 2) * Math.asin(c / a);
    return -(a * Math.pow(2, 10 * (t -= 1)) * Math.sin((t * d - s) * (Math.PI * 2) / p)) + b;
  },
  elasticOut (t, b, c, d, a, p) {
    var s;
    if (t == 0) return b;
    if ((t /= d) == 1) return b + c;
    if (!p) p = d * .3;
    if (!a || a < Math.abs(c)) {
      a = c;
      s = p / 4;
    }
    else s = p / (Math.PI * 2) * Math.asin(c / a);
    return (a * Math.pow(2, -10 * t) * Math.sin((t * d - s) * (Math.PI * 2) / p) + c + b);
  },
  elasticInOut (t, b, c, d, a, p) {
    var s;
    if (t == 0) return b;
    if ((t /= d / 2) == 2) return b + c;
    if (!p) p = d * (.3 * 1.5);
    if (!a || a < Math.abs(c)) {
      a = c;
      s = p / 4;
    }
    else s = p / (Math.PI * 2) * Math.asin(c / a);
    if (t < 1) return -.5 * (a * Math.pow(2, 10 * (t -= 1)) * Math.sin((t * d - s) * (Math.PI * 2) / p)) + b;
    return a * Math.pow(2, -10 * (t -= 1)) * Math.sin((t * d - s) * (Math.PI * 2) / p) * .5 + c + b;
  },
// Circular
  circularIn (t, b, c, d) {
    return -(c - b) * (Math.sqrt(1 - (t /= d) * t) - 1) + b;
  },
  circularOut (t, b, c, d) {
    return (c - b) * Math.sqrt(1 - (t = t / d - 1) * t) + b;
  },
  circularInOut (t, b, c, d) {
    if ((t /= d / 2) < 1) return -(c - b) / 2 * (Math.sqrt(1 - t * t) - 1) + b;
    return (c - b) / 2 * (Math.sqrt(1 - (t -= 2) * t) + 1) + b;
  },
// Back
  backIn (t, b, c, d, s = 1.70158) {
    return (c - b) * (t /= d) * t * ((s + 1) * t - s) + b;
  },
  backOut (t, b, c, d, s = 1.70158) {
    return (c - b) * ((t = t / d - 1) * t * ((s + 1) * t + s) + 1) + b;
  },
  backInOut (t, b, c, d, s = 1.70158) {
    if ((t /= d / 2) < 1) return (c - b) / 2 * (t * t * (((s *= (1.525)) + 1) * t - s)) + b;
    return (c - b) / 2 * ((t -= 2) * t * (((s *= (1.525)) + 1) * t + s) + 2) + b;
  },
// Bounce
  bounceIn (t, b, c, d) {
    return (c - b) - easeOutBounce(d - t, 0, c, d) + b;
  },
  bounceOut (t, b, c, d) {
    if ((t /= d) < (1 / 2.75)) {
      return (c - b) * (7.5625 * t * t) + b;
    } else if (t < (2 / 2.75)) {
      return (c - b) * (7.5625 * (t -= (1.5 / 2.75)) * t + .75) + b;
    } else if (t < (2.5 / 2.75)) {
      return (c - b) * (7.5625 * (t -= (2.25 / 2.75)) * t + .9375) + b;
    } else {
      return (c - b) * (7.5625 * (t -= (2.625 / 2.75)) * t + .984375) + b;
    }
  },
  bounceInOut (t, b, c, d) {
    if (t < d / 2) return easeInBounce(t * 2, 0, c, d) * .5 + b;
    else return easeOutBounce(t * 2 - d, 0, c, d) * .5 + c * .5 + b;
  },
// Cubic
  cubicIn (t, b, c, d) {
    return (c - b) * (t /= d) * t * t + b;
  },
  cubicOut (t, b, c, d) {
    return (c - b) * ((t = t / d - 1) * t * t + 1) + b;
  },
  cubicInOut (t, b, c, d) {
    if ((t /= d / 2) < 1) return (c - b) / 2 * t * t * t + b;
    return (c - b) / 2 * ((t -= 2) * t * t + 2) + b;
  }
}