export default {
  data () {
    return {
      validQuiz: true,
      anonymousQuiz: true,
      defaultOptions: {
        // Question level
        multiSelect: false,
        autoSubmit: false,
        distractorFeedback: false,
        noFeedback: false,
        reviewQuestion: false,
        highlightFeedback: false,
        randomizeDistractors: false,
        // KnowledgeCheck level
        randomizeQuestions: false,
        freshQuestions: 'ignore', // 'always', 'preferred'
        questionAttempts: 1,
        questionTotal: 0,
        hideRetakeOnPass: false,
        passingPercentage: 0,
        recordScore: false,
        recordInteraction: false,
        completeCourse: false,
        // Named Quiz level
        quizAttempts: 0
      },
      defaultPool: { total: 0, pass: 0, group: '' }
    }
  },
  methods: {
    getQuestionList () {
      if (typeof this.settings === 'object') {
        this.anonymousQuiz = true
        this.generateQuestionList()
      } else {
        this.anonymousQuiz = false
        if (this.$store.getters.isQuizInProgress(this.settings)) { // determine if named quiz is in progress
          this.quizList = this.$store.getters.getQuestionList(this.settings)
          this.position = this.$store.getters.getQuizPosition(this.settings)
          let quiz = this.$store.getters.getQuiz(this.settings)
          this.quizOptions = Object.assign({}, this.defaultOptions, quiz.options)
          this.currentQuestion = this.getQuestion()
        } else {
          this.generateQuestionList()
        }
      }
    },
    generateQuestionList () {
      let lastQuiz = null
      let contents
      let myQuestionList = []
      if (this.anonymousQuiz) { // determine if knowledgeCheck is using named or anonymous quiz. Set quizOptions and contents
        this.quizOptions = Object.assign({}, this.defaultOptions, this.settings.options)
        contents = this.settings.contents
      } else {
        lastQuiz = (this.$store.getters.getQuizHistory(this.settings).length > 0)? this.$store.getters.getQuizHistory(this.settings)[this.$store.getters.getQuizHistory(this.settings).length - 1] : null
        let quiz = this.$store.getters.getQuiz(this.settings)
        if (quiz === false) {
          this.validQuiz = false
          return
        }
        this.quizOptions = Object.assign({}, this.defaultOptions, quiz.options)
        contents = quiz.contents
      }
      if (this.quizOptions.quizAttempts !== 0 && this.quizOptions.quizAttempts <= this.$store.getters.getQuizHistory(this.settings).length) { //If all quiz attempts have been used
        this.quizList = this.$store.getters.getQuizHistory(this.settings)[this.$store.getters.getQuizHistory(this.settings).length - 1]
        this.showResults = true
        return
      }
      for (let i = 0; i < contents.length; i++) { // Parse quiz contents to create questionList
        let element = contents[i]
        if (typeof element === 'object') { // If content item is a pool
          element = Object.assign({}, this.defaultPool, contents[i])
          let poolHistory = this.$store.getters.getPoolHistory(element.name)
          let total = (element.total > 0) ? element.total : poolHistory.length
          let numberCorrect = 0
          let numberCorrectLastTime = 0
          let numberAskedLastTime = 0
          for (let k = 0; k < poolHistory.length; k++) { // Check number of correct questions from pool
            let poolHistoryItem = poolHistory[k]
            if (poolHistoryItem.history.length > 0) {
              if (lastQuiz !== null) {
                if (lastQuiz.indexOf(poolHistoryItem.name) > -1) {
                  numberAskedLastTime++
                }
              }
              if (poolHistoryItem.history[poolHistoryItem.history.length - 1].correct) {
                numberCorrect++
                if (lastQuiz !== null) {
                  if (lastQuiz.indexOf(poolHistoryItem.name) > -1) {
                    numberCorrectLastTime++
                  }
                }

              }
            }
          }
          let poolPercentCorrect = 0
          if (lastQuiz === null) { // Determine if pool passing should be calculated based on poolHistory or last quiz attempt
            poolPercentCorrect = (numberCorrect > 0) ? Math.floor((numberCorrect / total) * 100) : 0
          }
          else if (numberAskedLastTime === 0) {
            poolPercentCorrect = (numberCorrect > 0) ? Math.floor((numberCorrect / total) * 100) : 0
          } else {
            poolPercentCorrect = (numberCorrectLastTime > 0) ? Math.floor((numberCorrectLastTime / total) * 100) : 0
          }
          if (element.pass === 0 || element.pass > poolPercentCorrect) { // Check whether to skip pool
            let pool = this.randomize(this.$store.getters.getPool(element.name))
            let poolArray = []
            let maybeArray = []
            if (this.quizOptions.freshQuestions === 'always') { // If freshQuestion is set to always
              for (let j = 0; j < pool.length; j++) {
                let history = this.$store.getters.getQuestionHistory(pool[j])
                if (history.length === 0) {
                  poolArray.push(pool[j])
                  if (total === poolArray.length) { // Stop if pool total has been met
                    break
                  }
                }
              }
            } else if (this.quizOptions.freshQuestions === 'preferred') { // If freshQuestion is set to preferred
              for (let j = 0; j < pool.length; j++) {
                let history = this.$store.getters.getQuestionHistory(pool[j])
                if (history.length === 0) {
                  poolArray.push(pool[j])
                  if (total === poolArray.length) { // Stop if pool total has been met
                    break
                  }
                } else {
                  maybeArray.push(pool[j])
                }
              }
              if (poolArray.length < total) {
                let countDiff = total - poolArray.length
                poolArray = poolArray.concat(maybeArray.slice(0, countDiff))
              }
            } else { // If freshQuestion is set to ignore
              poolArray = pool.slice(0, total)
            }
            myQuestionList = myQuestionList.concat(poolArray) // add poolArray to questionList
          }
        } else { // If content item is a question
          if (this.quizOptions.freshQuestions !== 'always') {
            myQuestionList.push(element)
          } else {
            let questionHistory = this.$store.getters.getQuestionHistory(element)
            if (questionHistory.length === 0) {
              myQuestionList.push(element)
            }
          }
        }
      }
      if (myQuestionList.length > this.quizOptions.questionTotal && this.quizOptions.questionTotal !== 0) { // Limit questionList length to option.QuestionTotal
        myQuestionList = myQuestionList.slice(0, this.this.quizOptions.questionTotal)
      }
      this.quizList = (this.quizOptions.randomizeQuestions) ? this.randomize(myQuestionList) : myQuestionList
      this.position = 0
      if (this.quizList.length !== 0) {
        this.currentQuestion = this.getQuestion()
        if (!this.anonymousQuiz) {
          this.$store.commit('setQuestionList', { name: this.settings, list: myQuestionList })
          this.$store.commit('setQuizPosition', { name: this.settings, position: 0 })
        }
      } else{
        this.quizList = this.$store.getters.getQuizHistory(this.settings)[this.$store.getters.getQuizHistory(this.settings).length - 1]
        this.showResults = true
      }
    }
  }
}